
.App{
    display:flex;
    justify-content:center;
    align-items:center;
    background-color: black;
    height: 100vh;
}
.iconButton{
transition: 0.3s !important;
margin: 3px !important;
}
.iconButton:hover{
    transform: scale(1.1);
    box-shadow: 0 7px 7px rgba(0, 0, 0, 0.4);
}
.iconButton:active{
    transform: scale(0.9);
}

.todoCard{
    transition: 0.3s !important;
}
.todoCard:hover{
    padding: 10px 0;
    box-shadow: 0 12px 12px rgba(0, 0, 0, 0.5)
}

@font-face {
    font-family: Oswald;
    src: url(../public/fonts/Oswald-Bold.ttf);
    font-weight: bold;
}
@font-face {
    font-family: Oswald;
    src: url(../public/fonts/Oswald-Regular.ttf);
    font-weight: normal;
}
@font-face {
    font-family: Oswald;
    src: url(../public/fonts/Oswald-Medium.ttf);
    font-weight: medium;
}
